import React, {useEffect, useState} from "react";
import styles from 'assets/jss/info_user/rulePage'
import {makeStyles} from "@material-ui/core/styles";
import consts, {PRIMARY_COLOR} from "../../consts";
import Loading from "../../components/Loading";
import {useTranslation} from "react-i18next";

const useStyles = makeStyles(styles);

const defaultRule = '<div>-&emsp;ng k dch v: gi cc u gi: 2MT, gi cc c gia hn t ng hng ngy.<br/> -&emsp;Tham gia u gi: mi ngy thu bao c 5 ln t gi min ph cho cc sn phm trong cc phin u gi, t gi thm s tnh cc 1MT/1 ln. Cc ph c tr t ti khon di ng ca thu bao trn mng Movitel<br/>-&emsp;Cch chi u gi:<br/> &emsp;Gi tin nhn SMS: u s 86156<br/>&emsp;Tng tc USSD: u s 156<br/>&emsp;Truy cp qua website ca chng trnh: http://adivinhala.co.mz</div>';

function RuleInfoPage(props) {
    const classes = useStyles();
    const {getGuide, rule, title} = props;
    const {t} = useTranslation('info');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        getGuide({
            isdn: "2096091021",
            gameCode: consts.GAME_CODE,
            language: 'en',
        }, () => {
            setLoading(false);
        })
    }, [])

    return (
        <div className={classes.container}>
            {loading ? <Loading/> : <>
                <span style={{color: PRIMARY_COLOR, fontSize: 13, fontWeight: 600,}}>{title}</span>
                <div className={classes.content} dangerouslySetInnerHTML={{__html: `${rule || defaultRule}`}}/>
            </>
            }
        </div>
    )
}

export default RuleInfoPage;
